import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import classes from './SetUpStage.module.css';
import * as actionTypes from '../../../store/actionTypes/actionTypes';


const SetUpStage = () => {

    const dispatch = useDispatch();
    const [axis, setAxis] = useState(true)
    const toggleAxis = () => setAxis(!axis)
    const shipsArray = useSelector(state => state.frndlshpsrdcr.friendlyShips)

    const [counter, setCounter] = useState(0);

    const increaseCounter = () => {
        if(counter < 3) {
            setCounter(counter + 1)
        }
    }

    const placeShips = (placement, squares) => {
        let placementArray = [];
        if(axis) {
            for(let i=0; i<squares; i++) {
                placementArray.push(placement + i)
            }
        } else {
            for(let i=0; i<squares; i++) {
                placementArray.push(placement - (i*10))
            }
        }

        dispatch({type: actionTypes.ADD_PLACEMENT, counter: counter, placementArray: placementArray})

    }
    let numberOfPlacements = []
    let gridArray =[]
    for(let i = 0; i < 100; i++) {
        let div = <div className={classes.Square} key={i} onClick={()=>{
            placeShips(i, shipsArray[counter].squares);
            increaseCounter();
            
        }}></div>


        //Y axis initial disabled
        if(!axis && shipsArray[counter].squares === 2) {
            switch (i) {
                case 0:
                    case 1:
                        case 2:
                            case 3:
                                case 4:
                                    case 5:
                                        case 6:
                                            case 7:
                                                case 8:
                                                    case 9:

                    div = <div className={classes.SquareDisabled} key={i}></div>
                    break;
            
                default:
                    break;
            }
        }
        if(!axis && shipsArray[counter].squares === 3) {
            switch (i) {
                case 0:
                    case 1:
                        case 2:
                            case 3:
                                case 4:
                                    case 5:
                                        case 6:
                                            case 7:
                                                case 8:
                                                    case 9:
                                                        case 10:
                    case 11:
                        case 12:
                            case 13:
                                case 14:
                                    case 15:
                                        case 16:
                                            case 17:
                                                case 18:
                                                    case 19:


                    div = <div className={classes.SquareDisabled} key={i}></div>
                    break;
            
                default:
                    break;
            }
        }
        if(!axis && shipsArray[counter].squares === 4) {
            switch (i) {
                case 0:
                    case 1:
                        case 2:
                            case 3:
                                case 4:
                                    case 5:
                                        case 6:
                                            case 7:
                                                case 8:
                                                    case 9:
                                                        case 10:
                    case 11:
                        case 12:
                            case 13:
                                case 14:
                                    case 15:
                                        case 16:
                                            case 17:
                                                case 18:
                                                    case 19:
                                                        case 20:
                    case 21:
                        case 22:
                            case 23:
                                case 24:
                                    case 25:
                                        case 26:
                                            case 27:
                                                case 28:
                                                    case 29:


                    div = <div className={classes.SquareDisabled} key={i}></div>
                    break;
            
                default:
                    break;
            }
        }
        if(!axis && shipsArray[counter].squares === 5) {
            switch (i) {
                case 0:
                    case 1:
                        case 2:
                            case 3:
                                case 4:
                                    case 5:
                                        case 6:
                                            case 7:
                                                case 8:
                                                    case 9:
                                                        case 10:
                    case 11:
                        case 12:
                            case 13:
                                case 14:
                                    case 15:
                                        case 16:
                                            case 17:
                                                case 18:
                                                    case 19:
                                                        case 20:
                    case 21:
                        case 22:
                            case 23:
                                case 24:
                                    case 25:
                                        case 26:
                                            case 27:
                                                case 28:
                                                    case 29:
                                                        case 30:
                    case 31:
                        case 32:
                            case 33:
                                case 34:
                                    case 35:
                                        case 36:
                                            case 37:
                                                case 38:
                                                    case 39:


                    div = <div className={classes.SquareDisabled} key={i}></div>
                    break;
            
                default:
                    break;
            }
        }
        //X axis initial disabled
        if(axis && shipsArray[counter].squares === 2) {
            switch (i) {
                case 9:
                    case 19:
                        case 29:
                            case 39:
                                case 49:
                                    case 59:
                                        case 69:
                                            case 79:
                                                case 89:
                                                    case 99:

                    div = <div className={classes.SquareDisabled} key={i}></div>
                    break;
            
                default:
                    break;
            }
        }
        if(axis && shipsArray[counter].squares === 3) {
            switch (i) {
                case 9:
                    case 19:
                        case 29:
                            case 39:
                                case 49:
                                    case 59:
                                        case 69:
                                            case 79:
                                                case 89:
                                                    case 99:
                                                        case 8:
                                                            case 18:
                                                                case 28:
                                                                    case 38:
                                                                        case 48:
                                                                            case 58:
                                                                                case 68:
                                                                                    case 78:
                                                                                        case 88:
                                                                                            case 98:

                    div = <div className={classes.SquareDisabled} key={i}></div>
                    break;
            
                default:
                    break;
            }
        }
        if(axis && shipsArray[counter].squares === 4) {
            switch (i) {
                case 9:
                    case 19:
                        case 29:
                            case 39:
                                case 49:
                                    case 59:
                                        case 69:
                                            case 79:
                                                case 89:
                                                    case 99:
                                                        case 8:
                                                            case 18:
                                                                case 28:
                                                                    case 38:
                                                                        case 48:
                                                                            case 58:
                                                                                case 68:
                                                                                    case 78:
                                                                                        case 88:
                                                                                            case 98:
                                                                                                case 7:
                                                                                                    case 17:
                                                                                                        case 27:
                                                                                                            case 37:
                                                                                                                case 47:
                                                                                                                    case 57:
                                                                                                                        case 67:
                                                                                                                            case 77:
                                                                                                                                case 87:
                                                                                                                                    case 97:                                                                                                

                    div = <div className={classes.SquareDisabled} key={i}></div>
                    break;
            
                default:
                    break;
            }
        }
        if(axis && shipsArray[counter].squares === 5) {
            switch (i) {
                case 9:
                    case 19:
                        case 29:
                            case 39:
                                case 49:
                                    case 59:
                                        case 69:
                                            case 79:
                                                case 89:
                                                    case 99:
                                                        case 8:
                                                            case 18:
                                                                case 28:
                                                                    case 38:
                                                                        case 48:
                                                                            case 58:
                                                                                case 68:
                                                                                    case 78:
                                                                                        case 88:
                                                                                            case 98:
                                                                                                case 7:
                                                                                                    case 17:
                                                                                                        case 27:
                                                                                                            case 37:
                                                                                                                case 47:
                                                                                                                    case 57:
                                                                                                                        case 67:
                                                                                                                            case 77:
                                                                                                                                case 87:
                                                                                                                                    case 97:  
                                                                                                                                    case 6:
                    case 16:
                        case 26:
                            case 36:
                                case 46:
                                    case 56:
                                        case 66:
                                            case 76:
                                                case 86:
                                                    case 96:                                                                                              

                    div = <div className={classes.SquareDisabled} key={i}></div>
                    break;
            
                default:
                    break;
            }
        }

        let notAllowedSquares = []
        
        shipsArray.forEach(element => {
            element.placement.forEach(e => notAllowedSquares.push(e))
        });

        //Y axis not allowed squares 
        if(!axis && shipsArray[counter].squares === 2) {
            notAllowedSquares.forEach(element => {
                if(element + 10 === i || element + 20 === i) {
                    div = <div className={classes.SquareDisabled} key={i}></div>
                }
            })
        }
        if(!axis && shipsArray[counter].squares === 3) {
            notAllowedSquares.forEach(element => {
                if(element + 10 === i || element + 20 === i ) {
                    div = <div className={classes.SquareDisabled} key={i}></div>
                }
            })
        }
        if(!axis && shipsArray[counter].squares === 4) {
            notAllowedSquares.forEach(element => {
                if(element + 10 === i || element + 20 === i || element + 30 === i ) {
                    div = <div className={classes.SquareDisabled} key={i}></div>
                }
            })
        }
        if(!axis && shipsArray[counter].squares === 5) {
            notAllowedSquares.forEach(element => {
                if(element + 10 === i || element + 20 === i || element + 30 === i || element + 40 === i) {
                    div = <div className={classes.SquareDisabled} key={i}></div>
                }
            })
        }

        //X axis not allowed squares
        if(axis && shipsArray[counter].squares === 2) {
            notAllowedSquares.forEach(element => {
                if(element - 1 === i || element - 2 === i) {
                    div = <div className={classes.SquareDisabled} key={i}></div>
                }
            })
        }
        if(axis && shipsArray[counter].squares === 3) {
            notAllowedSquares.forEach(element => {
                if(element - 1 === i || element - 2 === i ) {
                    div = <div className={classes.SquareDisabled} key={i}></div>
                }
            })
        }
        if(axis && shipsArray[counter].squares === 4) {
            notAllowedSquares.forEach(element => {
                if(element - 1 === i || element - 2 === i || element - 3 === i ) {
                    div = <div className={classes.SquareDisabled} key={i}></div>
                }
            })
        }
        if(axis && shipsArray[counter].squares === 5) {
            notAllowedSquares.forEach(element => {
                if(element - 1 === i || element -2  === i || element - 3 === i || element - 4 === i) {
                    div = <div className={classes.SquareDisabled} key={i}></div>
                }
            })
        }


        if(notAllowedSquares.includes(i)){
            div = <div className={classes.SquarePlaced} key={i}></div>
        }
        gridArray.push(div)
    }
    shipsArray.forEach(element => {
        element.placement.forEach(e => numberOfPlacements.push(e))
    });

    const lockIn = () => { 
        dispatch({type: actionTypes.LOCKED_IN})
    }
    console.log("numner ogf placements" + numberOfPlacements)

    let shipClass = counter === 0 ? <img alt="ship" className={classes.ShipPicOne} src={shipsArray[counter].pathway}></img> : null
    if (counter === 1) { shipClass = <img alt="ship" className={classes.ShipPicTwo} src={shipsArray[counter].pathway}></img> }
    if (counter === 2) { shipClass = <img alt="ship" className={classes.ShipPicThree} src={shipsArray[counter].pathway}></img> }
    if (counter === 3) { shipClass = <img alt="ship" className={classes.ShipPicFour} src={shipsArray[counter].pathway}></img> }
    return (
        <div className={classes.SetUpContainer}>
            <button className={classes.AxisButton} onClick={toggleAxis}>{axis ? 'X ' : 'Y '} Axis</button>

            <div className={classes.Middle}>
                
                <p>Place your ships</p>
                {counter <= 3 ? shipClass : null}
            
                
            
                    <div className={classes.GridContainer} >
                        {gridArray.map((ele) => ele)}
                    </div>

            </div>


            {numberOfPlacements.length === 14 ? <button className={classes.SubmitButton}  onClick={lockIn}>Start</button> : <button className={classes.SubmitButton} disabled={true} >Start</button> }
        </div>
    )
}


export default SetUpStage
