import React from 'react';
import { useSelector } from 'react-redux';
import SetUpStage from './Components/SetUpStage/SetUpStage';
import GameStart from './Components/GameStart/GameStart';
import Battle from './Components/Battle/Battle';


const ShipsOfBattle = () => {

    const isLockedIn = useSelector(state => state.lckdnrdcr.lockedIn)
    const isGameStart = useSelector(state => state.gmstrtrdcr.gameStart)
    let onScreen = isGameStart ? <SetUpStage /> : <GameStart />
    if(isLockedIn) {
        onScreen = <Battle />
    }

    return (
        <div>
            {onScreen}
        </div>
    )
}

export default ShipsOfBattle